:host {
  display: flex;
  align-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
}

@media (min-width: 576px) {
  :host {
    flex-direction: row;
  }
}

:host([pfe-stacked]:not([pfe-stacked="false"])) {
  flex-direction: column;
}

:host([pfe-stacked]:not([pfe-stacked="false"])[pfe-centered]:not([pfe-centered="false"])) {
  align-items: center;
  text-align: center;
}

.pfe-icon-panel__content {
  margin-top: var(--pfe-theme--container-spacer, 16px);
  margin-left: 0;
}

@media (min-width: 576px) {
  .pfe-icon-panel__content {
    margin-top: 0;
    margin-left: var(--pfe-theme--container-spacer, 16px);
  }
}

:host([pfe-stacked]:not([pfe-stacked="false"])) .pfe-icon-panel__content {
  margin-top: var(--pfe-theme--container-spacer, 16px);
  margin-left: 0;
}

.pfe-icon-panel__footer {
  margin-top: var(--pfe-theme--content-spacer, 24px);
}
